            </AddSecondaryManager>

            <AddMembers v-if="isVisibleAddMemberPopUp" :type="type" :UserId="UserId"></AddMembers>

            <AssignManager v-if="isVisibleAssigManager" :CurrentUserDetails="userDetail" :type="type" :UserId="UserId"
