<script>

import AddSecondaryManager from '../../components/Modals/AddSecondaryManager.vue';
import AddMembers from '../../components/Modals/AddMembers.vue';
import DataService from '../../services/DataService';
import AssignManager from './AssignManager.vue';



export default {

    components: {

        AddSecondaryManager,
        AddMembers,
        AssignManager

    },

    props: {
        UserId: {
            type: Number
        },
        reqform:{
            type:String,
            required:false
        }


    },
    computed: {

        filteredPrimaryList() {
            const query = this.searchPriName.toLowerCase();
            return this.primaryReportTeam.filter(user => user.USER_NAME.toLowerCase().includes(query));
        },

        filteredSecondaryList() {
            const query = this.searchSecName.toLowerCase();
            return this.secondaryReportTeam.filter(user => user.USER_NAME.toLowerCase().includes(query));
        },



    },

    data() {

        return {
            searchPriName: '',
            searchSecName: '',
            secondaryUserIds: '',
            primaryUserIds: '',
            allPrimaryTeamSelect: false,
            allSecondaryTeamSelect: false,
            isVisibleSecondaryManagerPopUP: false,
            isVisibleAddMemberPopUp: false,
            userDetail: {},
            reportManagerDetail: [],
            isAddEditSecondaryPopup: false,
            secondaryReportTeam: [],
            primaryReportTeam: [],
            secondaryUserId: '',
            isPrimaryDeletebuttonVisible: false,
            isSecondaryDeletebuttonVisible: false,
            managertype: '',
            isVisibleAssigManager: false
        }
    },
    async created() {
        var vm = this;
        await vm.GetUserDetailByUserId();
        await vm.GetReportManager();
        await vm.GetOrgReportTeam();
        await vm.GetOrgReportSecondaryTeam();
    },
    methods: {


        selectAllPrimaryMemeber() {

            var vm = this;
            let userDetails = []
            let userIds = '';
            let obj = {};
            vm.allPrimaryTeamSelect = !vm.allPrimaryTeamSelect;
            if (vm.allPrimaryTeamSelect) {
                vm.primaryReportTeam.forEach(ele => {
                    ele.isSelected = true;
                })
                vm.isPrimaryDeletebuttonVisible = true;
            } else {
                vm.primaryReportTeam.forEach(element => {
                    element.isSelected = false;
                })
                vm.isPrimaryDeletebuttonVisible = false;
            }
            vm.primaryReportTeam.forEach(ele => {
                if (ele.isSelected == true) {
                    obj = {
                        USER_NAME: ele.USER_NAME,
                        userId: ele.user_id
                    }
                    userDetails.push(obj);
                }
            })
            userIds = userDetails.map(item => item.userId).join(',');

            vm.getPrimaryMemberIds(userIds);

        },


        selectPrimaryMemeber(event, item) {
            var vm = this;
            let userDetails = []
            let userIds = '';
            let obj = {};

            if (event.target.checked == true) {
                vm.primaryReportTeam.forEach(element => {
                    if (element.user_id == item.user_id) {
                        element.isSelected = true;
                        vm.allPrimaryTeamSelect = false;
                    } else {
                        //
                    }
                })

            }
            else {

                vm.primaryReportTeam.forEach(element => {
                    if (element.user_id == item.user_id) {
                        element.isSelected = false;
                        vm.allPrimaryTeamSelect = false;
                    } else {
                        //
                    }
                })


            }
            vm.primaryReportTeam.forEach(ele => {
                if (ele.isSelected == true) {
                    obj = {
                        USER_NAME: ele.USER_NAME,
                        userId: ele.user_id
                    }
                    userDetails.push(obj);
                }
            })
            if (vm.primaryReportTeam.length == userDetails.length) {
                vm.allPrimaryTeamSelect = true;
            }
            else {
                vm.allPrimaryTeamSelect = false;
            }
            userIds = userDetails.map(item => item.userId).join(',');
            if (userIds == "") {
                vm.isPrimaryDeletebuttonVisible = false;
            }
            else {
                vm.isPrimaryDeletebuttonVisible = true;
            }
            vm.getPrimaryMemberIds(userIds);

        },

        getPrimaryMemberIds(userIds) {
            var vm = this;
            vm.primaryUserIds = '';
            vm.primaryUserIds = userIds;
            console.log("getPrimaryMemberIds", vm.primaryUserIds);
        },



        selectAllSecondayMemeber() {

            var vm = this;
            let userDetails = []
            let userIds = '';
            let obj = {};
            vm.allSecondaryTeamSelect = !vm.allSecondaryTeamSelect;
            if (vm.allSecondaryTeamSelect) {
                vm.secondaryReportTeam.forEach(ele => {
                    ele.isSelected = true;
                })
                vm.isSecondaryDeletebuttonVisible = true;
            } else {
                vm.secondaryReportTeam.forEach(element => {
                    element.isSelected = false;
                })
                vm.isSecondaryDeletebuttonVisible = false;
            }
            vm.secondaryReportTeam.forEach(ele => {
                if (ele.isSelected == true) {
                    obj = {
                        USER_NAME: ele.USER_NAME,
                        userId: ele.user_id
                    }
                    userDetails.push(obj);
                }
            })
            userIds = userDetails.map(item => item.userId).join(',');
            vm.getSecondaryMemberIds(userIds);

        },


        selectSecondaryMemeber(event, item) {
            var vm = this;
            let userDetails = []
            let userIds = '';
            let obj = {};

            if (event.target.checked == true) {
                vm.secondaryReportTeam.forEach(element => {
                    if (element.user_id == item.user_id) {
                        element.isSelected = true;
                        vm.allSecondaryTeamSelect = false;
                    } else {
                        //
                    }
                })

            }
            else {

                vm.secondaryReportTeam.forEach(element => {
                    if (element.user_id == item.user_id) {
                        element.isSelected = false;
                        vm.allSecondaryTeamSelect = false;
                    } else {
                        //
                    }
                })


            }
            vm.secondaryReportTeam.forEach(ele => {
                if (ele.isSelected == true) {
                    obj = {
                        USER_NAME: ele.USER_NAME,
                        userId: ele.user_id
                    }
                    userDetails.push(obj);
                }
            })
            if (vm.secondaryReportTeam.length == userDetails.length) {
                vm.allSecondaryTeamSelect = true;
            }
            else {
                vm.allSecondaryTeamSelect = false;
            }
            userIds = userDetails.map(item => item.userId).join(',');
            if (userIds == "") {
                vm.isSecondaryDeletebuttonVisible = false;
            }
            else {
                vm.isSecondaryDeletebuttonVisible = true;
            }
            vm.getSecondaryMemberIds(userIds);

        },

        getSecondaryMemberIds(userIds) {
            var vm = this;
            vm.secondaryUserIds = '';
            vm.secondaryUserIds = userIds;
            console.log("getSecondaryMemberIds", vm.secondaryUserIds);
        },


        AddPrimaryManager(type) {

            var vm = this;
            if (type == 'Primary') {
                vm.managertype = type;
                vm.isVisibleAssigManager = true;
            }

        },
        AddSecondaryManager() {
            var vm = this;
            vm.secondaryUserId = '';
            vm.isAddEditSecondaryPopup = false;
            vm.isVisibleSecondaryManagerPopUP = true;
        },

        AddMembers(type) {

            var vm = this;
            vm.type = type;
            vm.isVisibleAddMemberPopUp = true;
        },

        async DeleteMember(type, user_id) {
            var vm = this;

            try {
                const result = await new Promise((resolve) => {
                    vm.confirmR(vm.$t('Are you sure you want to delete these record(s)?'), true, false, resolve);
                });

                if (result != undefined) {
                    return; // If user cancels, exit the function
                }

                let url = `EmpId=${user_id}&type=${type}&userid=${vm.UserId}`;
                let response = await DataService.DeletePrimarySecondaryMember(url);
                debugger;
                if (response.data[0].Value == '1') {
                    vm.ShowAlert(vm.$t("Primary Member(s) has been Deleted Successfully"), "success", vm.$t('Alert'));
                    vm.primaryReportTeam = [];
                    vm.allPrimaryTeamSelect = false;
                    vm.isPrimaryDeletebuttonVisible = false
                    await vm.GetOrgReportTeam();
                } else if (response.data[0].Value == '2') {

                    vm.ShowAlert(vm.$t("Secondary Member(s) has been Deleted Successfully"), "success", vm.$t('Alert'));
                    vm.secondaryReportTeam = [];
                    vm.allSecondaryTeamSelect = false;
                    vm.isSecondaryDeletebuttonVisible = false
                    await vm.GetOrgReportSecondaryTeam();

                } else if (response.data[0].Value == '3') {
                    await vm.GetReportManager();
                    vm.ShowAlert(vm.$t("Primary Manager has been Deleted Successfully"), "success", vm.$t('Alert'));
                }
            } catch (error) {
                console.log("error message", error);
            }
        },

        async GetUserDetailByUserId() {
            var vm = this
            try {

                let url = `userId=${vm.UserId}`;

                await DataService.GetUserDetailById(url).then(response => {
                    vm.userDetail = response.data;
                })

            } catch (error) {
                console.log("error message", error);
            }
        },

        async GetReportManager() {
            var vm = this
            try {

                let url = `userId=${vm.UserId}`;
                await DataService.GetReportManager(url).then(response => {
                    vm.reportManagerDetail = response.data.result;
                })

            } catch (error) {
                console.log("error message", error);
            }
        },

        async GetOrgReportTeam() {
            var vm = this
            try {

                let url = `userId=${vm.UserId}`;

                await DataService.GetOrgReportTeam(url).then(response => {
                    vm.primaryReportTeam = [];
                    let data = JSON.parse(response.data[0].Value).Data;
                    data.forEach(element => {
                        vm.primaryReportTeam.push(element);
                    })

                    vm.primaryReportTeam.forEach(element => {
                        element.isSelected = false
                    })


                })

            } catch (error) {
                console.log("error message", error);
            }
        },

        async GetOrgReportSecondaryTeam() {
            var vm = this
            try {

                let url = `userId=${vm.UserId}`;

                await DataService.GetOrgReportSecondaryTeam(url).then(response => {
                    vm.secondaryReportTeam = [];
                    let data = JSON.parse(response.data[0].Value).Data;

                    data.forEach(element => {
                        vm.secondaryReportTeam.push(element);
                    })

                    vm.secondaryReportTeam.forEach(element => {
                        element.isSelected = false
                    })


                })

            } catch (error) {
                console.log("error message", error);
            }
        },


        EditManager(type, item) {

            var vm = this;

            if (item.Manager == 'Primary') {
                vm.managertype = item.Manager;
                vm.secondaryUserId = item.user_id;
                vm.isVisibleAssigManager = true;
            }
            else {
                vm.managertype = item.Manager;
                vm.secondaryUserId = item.user_id;
                vm.isAddEditSecondaryPopup = true;
                vm.isVisibleSecondaryManagerPopUP = true;

            }



        }

    },

}
</script>

