<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">

                        <div class="modal-header ui-draggable-handle pt-2">



                            <h4 v-if="managertype == 'Primary'" name="header" class="modal-title mt-2 pt-1">
                                {{ $t(' Manage Primary Manager') }}
                            </h4>

                            <h4 v-else name="header" class="modal-title mt-2 pt-1">
                                {{ $t('AssignManager') }}
                            </h4>


                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>

                        <div class="listing py-3 px-3">
                            <!-- 
                            <div class="orgpersondata col-sm-6 col-lg-6 float-left p-0">
                                <div class="col-12 float-left">
                                    <h5 class="mb-1">{{ defaultManagerDetail.USER_NAME }}</h5>
                                </div>
                                <div class="col-12 float-left">{{ defaultManagerDetail.designation_name }}</div>
                                <div class="col-12 float-left">{{ defaultManagerDetail.department_name }}</div>
                            </div> -->

                            <dynamic-form lang="en" ref="refPrimaryManager" :buttons="buttons" v-on:OnSubmit="onSubmit"
                                :schema="FormSchema"></dynamic-form>

                        </div>

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

import DataService from '../../services/DataService';
export default {

    props: {

        UserId: {
            type: Number
        },

        CurrentUserDetails: {

        },
        managertype: {
            type: String
        }

    },

    data() {
        return {

            FormSchema: [
                {
                    layoutType: "double",

                    Data: [
                        {
                            astype: "SelectField",
                            label: "Users",
                            name: "User",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                required: true,
                                max: 250,
                            },
                            config: {
                                options: [],
                               // onChange: this.checkAssignManager
                            },
                        },


                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2 '></i>Save",
                    // onClick: this.GetResetPasswordData
                },

            ],
            userList: [],
            userDetail: [],
            primaryManagerDetail: [],
            defaultManagerDetail: {}

        }
    },
    async created() {

        var vm = this;
        await vm.GetManagerDetails();
        // await vm.getPrimaryManagerDetail();
    },
    methods: {

        ClosePopUp() {
            var vm = this;
            vm.$parent.isVisibleAssigManager = false;
        },



        async getPrimaryManagerDetail() {

            var vm = this
            try {

                let url = `userId=${vm.UserId}`;
                await DataService.GetReportManager(url).then(response => {
                    vm.primaryManagerDetail = response.data.result;
                    vm.primaryManagerDetail.forEach(element => {
                        if (element.Manager == 'Primary') {
                            vm.defaultManagerDetail = element
                        }
                    })
                    // vm.defaultManagerDetail = response.data.result[0];
                })

            } catch (error) {
                console.log("error message", error);
            }
        },



        async GetManagerDetails() {
            var vm = this;
            try {

                let url = `userIds=${vm.UserId}`;
                await DataService.GetSecondaryManager(url).then(response => {
                    let userData = JSON.parse(response.data[0].Value).Data;
                    userData.forEach((item) => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: `${item.USER_NAME}`,
                            value: `${item.USER_ID}`,
                        });
                    });
                    vm.$refs.refPrimaryManager.UpdateKeyValue();
                })

            } catch (error) {
                console.log("error message", error);
            }
        },


        async checkUserDetailById(item) {
            var vm = this;
            vm.userDetail = [];
            try {
                const url = `userId=${item.value}`;
                const response = await DataService.GetUserDetailById(url);
                if (response.data != null || response.data != '') {

                    vm.userDetail.push(response.data)
                }

                return response.data; // Return data for further use
            } catch (error) {
                console.error("Error fetching user details:", error);
                throw error; // Ensure the error is propagated for proper handling
            }
        },


        // async checkAssignManager(event, item) {
        //     debugger;

        //     var vm = this;
        //     try {
        //         const userDetail = await vm.checkUserDetailById(item);

        //         if (userDetail) {

        //             const canAddTeam = await vm.checkUserAbleToAddTeam(vm.CurrentUserDetails.USER_TYPE_NAME);

        //             if (canAddTeam.length > 0) {

        //                 vm.FormSchema[0].Data[0].value = '';
        //                 vm.ShowAlert(
        //                     vm.$t(`You cannot add a (${canAddTeam}) user under ${this.userDetail[0].User_Name} team.`),
        //                     "failure",
        //                     vm.$t('Alert')
        //                 )

        //                 return false;
        //             }
        //             else {
        //                 //
        //             }
        //         }
        //     } catch (error) {
        //         console.error("Error checking assignment manager:", error);
        //     }
        // },


        // checkUserAbleToAddTeam(supervisorUserType) {
            
        //     const userNames = [];

        //     this.userDetail.forEach(item => {
        //         debugger
        //         // Ensure item.USER_TYPE_NAME is defined before using it
        //         if (item.USER_TYPE_NAME !== undefined) {
        //             if (supervisorUserType === "CA" && item.USER_TYPE_NAME !== "CA" && item.USER_TYPE_NAME !== "PM" && item.USER_TYPE_NAME !== "NU") {
        //                 userNames.push(`${item.User_Name} - ${item.USER_TYPE_NAME}`);

        //             } else if (supervisorUserType === "PM" && item.USER_TYPE_NAME !== "PM" && item.USER_TYPE_NAME !== "NU" && item.USER_TYPE_NAME !== "CA") {
        //                 userNames.push(`${item.User_Name} - ${item.USER_TYPE_NAME}`);

        //             } else if (supervisorUserType === "NU" && item.USER_TYPE_NAME == "NU") {
        //                 userNames.push(`${item.User_Name} - ${item.USER_TYPE_NAME}`);

        //             }
        //         }
        //     });

        //     return userNames.join(", ");
        // },



        async onSubmit(formfields) {

            var vm = this;
            try {

                let url = '';
                url = `newManager=${parseInt(formfields.User)}&userid=${vm.UserId}&type=AddPrimary&secid=`;

                await DataService.AddSecondaryManagerData(url).then(response => {

                    if (response.data[0].Value == '1') {
                        vm.ShowAlert(vm.$t("Primary Manager has been added successfully"), "success", vm.$t('Alert'));
                        if (vm.managertype == 'Primary') {
                            vm.$parent.GetReportManager();
                        }
                        vm.ClosePopUp();
                        vm.$parent.listheaderbuttons[2].isdisabled = true;
                        vm.$parent.FetchUserData();
                    }
                    if (response.data[0].Value == '2') {
                        //
                        vm.ShowAlert(vm.$t("Primary Manager has been changed successfully"), "success", vm.$t('Alert'));
                        if (vm.managertype == 'Primary') {
                            vm.$parent.GetReportManager();
                        }
                        vm.ClosePopUp();
                        vm.$parent.listheaderbuttons[2].isdisabled = true;
                        vm.$parent.FetchUserData();


                    }

                })

            } catch (error) {
                console.log("error message", error);
            }
        },

    },

}
</script>