<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1 ">
                            <h4 name="header" class="modal-title mt-2 pt-2 mb-2">
                                {{ $t('ResetPasswordHeading') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                            <span class="user-guide p-2 ">
                                <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                    role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                        class="fa fa-files-o pr-2"></em>User Guide</a>
                                <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                    <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('UserGuideResetPassword')"></div>
                                    </div>
                                </div>
                            </span>
                        </div>
                        <div class="listing py-3 px-3">
                            <loader :is-visible="isLoading"></loader>
                            <dynamic-form lang="en" ref="refResetPassword" :buttons="buttons"
                                :schema="FormSchema"></dynamic-form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        UserResetPasswordData: {
            type: Object
        }
    },
    data() {
        return {

            ResetPasswordByAdmin: [],
            token: null,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Email'),
                            name: "Email",
                            value: "",
                            placeholder: "Enter Title",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            disabled: true,
                            validationRules: "",
                        },
                        {
                            astype: "PasswordField",
                            label: this.$t('NewPassword'),
                            name: "NewPassword",
                            value: "",
                            placeholder: "Enter New Password",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: {
                                'required' : true,
                                'min':6,
                                'regex':/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+{}\[\]:;"'<>,.?/\\|`~]).*$/
                            },
                        },
                        {
                            astype: "PasswordField",
                            label: this.$t('ConfirmPassword'),
                            name: "ConfirmPassword",
                            value: "",
                            placeholder: "Enter Confirm Password",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: {
                                'required' : true,
                                'min':6,
                                'regex':/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*()_+{}\[\]:;"'<>,.?/\\|`~]).*$/
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Reset Password",
                    onClick: this.onSubmit
                },
                {
                    type: "submit",
                    class: "btn btn-primary disabled",
                    text: "<i class='fa fa-key pr-2'></i>Random Password",
                    onClick: this.GenerateRandomPassword
                }
            ],
            isLoading: false,
        }
    },
    async created() {

        var vm = this;
        vm.token = this.GetUserInfo.Token;
        vm.FormSchema[0].Data[0].value = vm.UserResetPasswordData.email_id;
        await vm.GetPasswordPolicyByCompanyId();
    },

    methods: {
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseUserResetPasswordPopUp();
        },
        async onSubmit(event, data) {
       
            var vm = this;
            vm.$refs.refResetPassword.validateForm.validate().then(async function (valid) {
                if(valid){
                    const newPassword = vm.FormSchema[0].Data[1].value;
                    const confirmPassword = vm.FormSchema[0].Data[2].value;

                    // const newPassword = event.NewPassword;
                    // const confirmPassword = event.ConfirmPassword;
                    //const specialChars = /[!@#$%^&*(),.?":{}|<>]/

                    if (newPassword !== confirmPassword) {
                
                        //alert(this.$t('Passwords do not match!'));
                        vm.ShowAlert(vm.$t("Passwords do not match Please enter the correct password!"), "danger", vm.$t('Alert'));

                        return;
                    }
                    else if (newPassword == confirmPassword) {
                    
                        //alert(this.$t('Passwords  match!'));
                        //return;
                    }
                    vm.isLoading = true;
                    try {
                        //const url = `UserId=${vm.UserResetPasswordData.user_id}&Email=${vm.UserResetPasswordData.email_id}&Token=${vm.token}&NewPassword=${newPassword}&ConfirmPassword=${confirmPassword}`;
                        let AdminResetPasswordModel = {
                            UserId: vm.UserResetPasswordData.user_id,
                            Email: vm.UserResetPasswordData.email_id,
                            Token: vm.token,
                            NewPassword: newPassword,
                            ConfirmPassword: confirmPassword
                        }
                        
                        //const response = await DataService.ResetPasswordByAdmin(url);
                        await DataService.ResetPasswordByAdmin(AdminResetPasswordModel).then(response => {
                            if (response.data.status === 'Success') {
                                vm.ShowAlert(vm.$t("Your password has been reset successfully. Please login with your new password"), "success", vm.$t('Alert'));

                            }
                            console.log(response);
                            vm.isLoading = false;
                            vm.ClosePopUp();
                        })

                    } catch (error) {
                        //console.error(error);
                        vm.isLoading = false;
                    }
                }
            })
            


        },
        async GetPasswordPolicyByCompanyId(){
            var vm = this;
            await DataService.GetPasswordPolicyByCompanyId().then(response=>{
                if(response.data != "" && response.data != null && response.data != undefined){
                    vm.buttons[1].class = response.data.is_generate_random_password_required ? 'btn btn-primary' : 'btn btn-primary disabled';
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        GenerateRandomPassword(x,y){
            var vm = this;
            var temp = {
                UserId: vm.UserResetPasswordData.user_id,
                Email: vm.UserResetPasswordData.email_id,
                Token: "",
                NewPassword: vm.FormSchema[0].Data[1].value,
                ConfirmPassword: vm.FormSchema[0].Data[2].value
            }
            vm.isLoading = true;
            DataService.GenerateRandomPassword(temp).then(response=>{
                if(response.data.status == 0 && response.data.result == 'Email'){
                    vm.ShowAlert(vm.$t("RandomPasswordSuccessMsg"), 'success', true, vm.$t('Alert'));
                }else if(response.data.status != 0 && response.data.result == 'Email'){
                    vm.ShowAlert(vm.$t("RandomPasswordMailFailureMsg",[vm.$t(response.data.password)]), 'failure', true, vm.$t('Alert'));
                }
                else{
                    vm.ShowAlert(vm.$t("AddedError",[vm.$t('User')]), 'failure', true, vm.$t('Alert'));
                }
                vm.isLoading = false;
            }).catch(err=>{
                console.log(err);
            })
        }
        // GenerateRandomPassword() {
        //     const randomPassword = Math.random().toString(36).slice(-8);
        //     this.FormSchema[0].Data[1].value = randomPassword;
        //     this.FormSchema[0].Data[2].value = randomPassword;
        // }
    },

}
</script>