<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle pt-2">

                            <h4 name="header" class="modal-title mt-2 pt-1">

                                {{ isAddEditSecondaryPopup ? $t('EditSecondarymanager') : $t('AddSecondarymanager') }}

                            </h4>


                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="listing py-3 px-3">
                            <loader :is-visible="isLoading"></loader>
                            <dynamic-form lang="en" ref="refSecondarySchema" :buttons="buttons" v-on:OnSubmit="onSubmit"
                                :schema="FormSchema"></dynamic-form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {

    props: {
        isAddEditSecondaryPopup: {
            type: Boolean
        },

        UserId: {
            type: Number
        },
        secondaryUserId: {
            type: Number

        },


        CurrentUserDetails: {

        },



    },

    data() {
        return {

            FormSchema: [
                {
                    layoutType: "double",

                    Data: [
                        {
                            astype: "SelectField",
                            label: "Users",
                            name: "User",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                required: true,
                                max: 250,
                            },
                            config: {
                                options: [],
                                onChange: this.checkAssignManager
                            },
                        },


                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2 '></i>Save",
                    // onClick: this.GetResetPasswordData
                },

            ],
            userList: []

        }
    },

    created() {
        var vm = this;
        vm.GetSecondaryManager();
        // vm.getCurrentUserDetailById();
        // console.log("CurrentUserDetails", vm.CurrentUserDetails);
    },

    methods: {

        ClosePopUp() {

            var vm = this;
            vm.$parent.isVisibleSecondaryManagerPopUP = false;

        },



        // async getCurrentUserDetailById() {
        //     debugger
        //     var vm = this;
        //     vm.CurrentUserDetails = [];
        //     try {

        //         const url = `userId=${vm.UserId}`;
        //         const response = await DataService.GetUserDetailById(url);

        //         if (response.data != null || response.data != '') {
        //             vm.CurrentUserDetails.push(response.data)
        //         }

        //         return
        //     } catch (error) {
        //         console.error("Error fetching user details:", error);

        //     }
        // },




        async checkUserDetailById(item) {
            
            var vm = this;
            vm.userDetail = [];
            try {
                const url = `userId=${item.value}`;
                const response = await DataService.GetUserDetailById(url);
                if (response.data != null || response.data != '') {

                    vm.userDetail.push(response.data)
                }

                return response.data; // Return data for further use
            } catch (error) {
                console.error("Error fetching user details:", error);
                throw error; // Ensure the error is propagated for proper handling
            }
        },


        async checkAssignManager(event, item) {

            var vm = this;
            try {
                const userDetail = await vm.checkUserDetailById(item);

                if (userDetail) {
                    const canAddTeam = await vm.checkUserAbleToAddTeam(vm.CurrentUserDetails.USER_TYPE_NAME);

                    if (canAddTeam.length > 0) {

                        vm.FormSchema[0].Data[0].value = '';
                        vm.ShowAlert(
                            vm.$t(`You cannot add a (${canAddTeam}) user under ${this.userDetail[0].User_Name} team.`),
                            "failure",
                            vm.$t('Alert')
                        )

                        return false;
                    }
                    else {
                        //
                    }
                }
            } catch (error) {
                console.error("Error checking assignment manager:", error);
            }
        },


        checkUserAbleToAddTeam(supervisorUserType) {
            const userNames = [];

            this.userDetail.forEach(item => {
                // Ensure item.USER_TYPE_NAME is defined before using it
                if (item.USER_TYPE_NAME !== undefined) {
                    if (supervisorUserType === "CA" && item.USER_TYPE_NAME !== "CA" && item.USER_TYPE_NAME !== "PM" && item.USER_TYPE_NAME !== "NU") {
                        userNames.push(`${item.User_Name} - ${item.USER_TYPE_NAME}`);
                        // Uncomment and use the following line if you need to show an alert
                        // ShowAlert("For " + item.USER_TYPE_NAME + " user - You cannot add an admin under your team. Please uncheck " + item.User_Name + " in the list", "error", true, Globalize.localize("Alert"));
                    } else if (supervisorUserType === "PM" && item.USER_TYPE_NAME !== "PM" && item.USER_TYPE_NAME !== "NU") {
                        userNames.push(`${item.User_Name} - ${item.USER_TYPE_NAME}`);
                        // Uncomment and use the following line if you need to show an alert
                        // ShowAlert("For " + item.USER_TYPE_NAME + " user - You cannot add an admin under your team. Please uncheck " + item.User_Name + " in the list", "error", true, Globalize.localize("Alert"));
                    } else if (supervisorUserType === "NU" && item.USER_TYPE_NAME !== "NU") {
                        userNames.push(`${item.User_Name} - ${item.USER_TYPE_NAME}`);
                        // Uncomment and use the following line if you need to show an alert
                        // ShowAlert("For " + item.USER_TYPE_NAME + " user - You cannot add an admin under your team. Please uncheck " + item.User_Name + " in the list", "error", true, Globalize.localize("Alert"));
                    }
                }
            });

            return userNames.join(", ");
        },



        async GetSecondaryManager() {
            var vm = this;
            try {

                let url = `userIds=${vm.UserId}`;
                await DataService.GetSecondaryManager(url).then(response => {
                    let userData = JSON.parse(response.data[0].Value).Data;

                    userData.forEach((item) => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: `${item.USER_NAME}`,
                            value: `${item.USER_ID}`,
                        });
                    });
                    vm.$refs.refSecondarySchema.UpdateKeyValue();
                })

            } catch (error) {
                console.log("error message", error);
            }
        },


        async onSubmit(formfields) {

            var vm = this;
            try {

                let url = '';
                if (vm.secondaryUserId != "") {
                    //
                    url = `newManager=${parseInt(formfields.User)}&userid=${vm.UserId}&type=Secondary&secid=${vm.secondaryUserId}`;

                }
                else {
                    url = `newManager=${parseInt(formfields.User)}&userid=${vm.UserId}&type=AddSecondary&secid=${vm.secondaryUserId}`;

                }

                await DataService.AddSecondaryManagerData(url).then(response => {

                    if (response.data[0].Value == '1') {
                        vm.$parent.GetReportManager();
                        vm.ShowAlert(vm.$t("Secondary Manager has been added successfully"), "success", vm.$t('Alert'));
                        vm.ClosePopUp();
                    }
                    if (response.data[0].Value == '2') {
                        //
                        vm.$parent.GetReportManager();
                        vm.ShowAlert(vm.$t("Secondary Manager has been changed successfully"), "success", vm.$t('Alert'));
                        vm.ClosePopUp();
                    }

                })

            } catch (error) {
                console.log("error message", error);
            }
        },

    },

}
</script>