<template lang="en">
 <div class="right-content right-content-expand">
    <section id="main-content" class="main-content">
        <section class="main-content">
            <div class="container-fluid p-0">
                <div class="border p-3 col-lg-12">
                    <dynamic-form ref="dynamicForm"  lang="en" :buttons="buttons" :mandatorytext="$t('MandatoryString')" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                    </dynamic-form>
                </div>
                <div class="listing mt-2">
                     <!-- <div class="col-lg-6">
                        <div class="py-2">
                            <span class="mandatory">{{ $t("Mandatory") }}</span>
                        </div>
                    </div> -->
      
       <div class="conversation-date heading-border"><span>{{$t("ContactNumber")}}</span></div>
    
        <div class="row">
            <div class="col-12">
                <div class="d-inline-block">
                    <span class="legend-pending colorboxsquare"></span>{{ $t("Communication")}} {{ $t("VisibleToClient")}} 
                </div>
            </div>
        </div>
            <div class="table-responsive">
                <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                        <thead class="thead-bg">
                            <tr>
                                <th class="text-center">
                                    <div class="custom-control custom-checkbox">
                                        <input type="checkbox" class="AddressChkAll custom-control-input" id="chkAll_0" name="checkbox" v-model="selectAll" @change="toggleSelectAll" />
                                        <label class="custom-control-label universal-custom-control-label pt-1" for="chkAll_0"></label>
                                    </div>
                                </th>
                                <th class="text-center"><span>#</span></th>
                                <th class="text-left"><span>{{$t("ContactNumber")}}</span></th>
                                <th class="text-left"><span>{{$t("PhoneDescription")}}</span></th>
                                <th class="text-center"><span>{{$t("CompanyDirectory")}} </span></th>
                                <th class="text-center"><span>{{$t("Edit")}}</span></th>
                                <th class="text-center"><span>{{$t("Delete")}}</span></th>
                            </tr>
                        </thead>
                        <tbody>
                            
                             <tr v-for="(phone, index) in PhoneData.data" :key="phone.user_phone_id" :class="phone.is_visible == true ? 'text-center high-bar' : 'text-center'">
                                
                                <td class="text-center">
                                    <div class="custom-control custom-checkbox">
                                        <input type="checkbox" class="chkItems custom-control-input" :id="'chk_' + phone.user_phone_id" v-model="phone.selected" />
                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_' + phone.user_phone_id"></label>
                                    </div>
                                </td>                                                            
                                <td class="text-center">{{ phone.ROWNUMBER }}</td>
                                <td class="text-left">{{ phone.user_phone }}</td>
                                <td class="text-left">{{ phone.user_phone_desc }}</td>
                                <td class="text-center">{{ phone.add_to_directory  ? 'Yes' : 'No' }}</td>
                                <td class="text-center single-action"><a class="enable" data-placement="left" v-on:click="Edit(phone)"  href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Edit">
                                    <em class="fa fa-pencil text-success action-icon"></em></a>
                                </td>
                                <td class="text-center single-action"><a class="enable" data-placement="left" v-on:click="Delete(phone)"  href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Delete" aria-describedby="tooltip845041">
                                    <em class="fa fa-trash action-icon text-danger"></em></a>
                                </td>
                            </tr> 
                            <tr v-if="PhoneData.data.length==0">
                                <td colspan="7" class="text-danger text-center no-record"> {{ $t("NoRecordfound") }}</td>
                            </tr>
                        </tbody>
                    </table>                
            </div>
        </div>
            </div>
        </section>
    </section>
</div>
</template>



<script>
import { defineComponent} from '@vue/composition-api'
import { extend,ValidationObserver, ValidationProvider} from 'vee-validate';
// import { ConvertCustomFieldIntoDynamicFormObj,ConvertDynamicFormObjToCustomFieldObj} from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService';
extend('required', {
    validate(value) {        
        console.log("checkk")
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: 'The {_field_} is required'
    ///computesRequired: true
});

export default defineComponent({
    name: "Manage Contact",
    components: {
        ValidationObserver,
        ValidationProvider
    },
    props: {
        UserId:{
            type:Number
        },
        IsClient: {
            type: String
        }         
    },
    data() {
        return {            
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                            {
                                astype: "NumericField",
                                label: this.$t('ContactNumber'),
                                name: "ContactNumber",
                                value: "",
                                placeholder: this.$t('ContactNumber'),
                                validationRules: {
                                    "required": true
                                },
                                disabled: false,
                            },
                            {
                                astype: "TextField",
                                label: this.$t('PhoneDescription'),
                                name: "PhoneDescription",
                                value: "",
                                placeholder: this.$t('PhoneDescription'),
                                validationRules: {
                                    "required": true
                                },
                                disabled: false,
                            },
                            {
                                astype: "toggleField",
                                label: this.$t('IsinCompanyDirectory'),
                                name: "CompanyDirectory",
                                value: "",
                                placeholder: "",
                                validationRules: {
                                    "required": false
                                },
                            },                         
                            {
                                astype: "toggleField",
                                label: this.$t('VisibleToClient'),
                                name: "VisibleToClient",
                                value: "",
                                placeholder: "",
                                validationRules: {
                                    "required": false
                                },
                                 config: {
                                onChange: this.VisibleToClient
                            }
                            },                                                      
                       
                    ],
                },                               
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Clear,
                },
            ],
            //FormSchema: [],
            isLoading: false,
            phoneId:0,
            pageNo: 1,
            pageSize: 10,
            PhoneData:[]           
        }

    },

    created: function () {
        var vm = this;
        vm.FetchData();
        if(vm.IsClient=='Client'){
            vm.FormSchema[0].Data[3].visibility = false;
        }else{
            vm.FormSchema[0].Data[3].visibility = true;
        }    
    },
    methods: {
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            
            var params = `pageNum=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userId=${vm.UserId}`;
            DataService.GetUserPhoneList(params).then(function (response) {
                
                vm.PhoneData = JSON.parse(response.data);
            })
            
        },
        Edit:function(Item){
            var vm=this;
            vm.phoneId=Item.user_phone_id;
            (vm.FormSchema)[0].Data[0].value=Item.user_phone;
            (vm.FormSchema)[0].Data[1].value=Item.user_phone_desc;            
            (vm.FormSchema)[0].Data[2].value=Item.add_to_directory;
            if(vm.IsClient=='Client'){
                (vm.FormSchema)[0].Data[3].visibility= false;
            }else{
                (vm.FormSchema)[0].Data[3].value=Item.is_visible;
            }            
        },
        Delete:function(Item){
            var vm=this;
            vm.phoneId=Item.user_phone_id;
            if (vm.phoneId > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("AddtionalPhone")), function () {
                    var params = `phoneId=${vm.phoneId}&userId=${vm.UserId}`;
                    DataService.DeleteAddtionalPhone(params).then(function (response) { 
                                             
                        if (response.data.Message =='Deletion successful') {                            
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));                            
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t("Alert"));
                        }
                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        onSubmit: function (formfields) {
            var vm = this;
            var objData = {
                userPhoneId: vm.phoneId > 0 ? vm.phoneId : 0,
                userid: vm.UserId,               
                phone : formfields.ContactNumber,
                Description : formfields.PhoneDescription,                        
                isVisible: formfields.VisibleToClient,
                addToDir: formfields.CompanyDirectory,  
                isVisibletoCA:true                             
            }
            
            
           var jsonData= JSON.stringify(objData);
            DataService.AddUpdateContact(jsonData).then(response => {
                if (response.data != null) {
                    
                    const Data = JSON.parse(response.data);
                    if (response != null && response.data != null && response.data > 0) {
                    vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'), 'success');
                    vm.isLoading = false;
                    vm.Clear();
                    vm.FetchData();                   
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                    vm.isLoading = false;
                    vm.FetchData();
                }
                }
            });
        },

        Clear:function(){
            var vm=this;
            vm.phoneId=0;
            (vm.FormSchema)[0].Data[0].value='';
            (vm.FormSchema)[0].Data[1].value='';            
            (vm.FormSchema)[0].Data[2].value='';
            (vm.FormSchema)[0].Data[3].value='';
            vm.$refs.dynamicForm.validateForm.reset();            
        },

        // getForm: function (id) {
        //     var vm = this;
        //     vm.isLoading = true;
        //     var url = `recordId=${id}&moduleName=${this.ModuleName}&subModuleCode=${this.SubModule}`;
        //     DataService.GetDynamicForm(url).then(function (response) {
        //         if (response != null && response.data != null) {

        //             vm.isMapFeature = response.data.isFsmLocating;
        //             response.data.data
        //             vm.FormSchema = ConvertCustomFieldIntoDynamicFormObj(response.data.data)
        //             console.log("tis",vm.FormSchema)
        //             if(vm.FormSchema.length>0){
        //                vm.FormSchema[0].visibility= false
        //             }
        //             vm.isLoading = false;
        //         }
        //     });
        // },
        closePopup(){
            this.$emit('close')
          
        },        
        VisibleToClient() { 
            var vm = this;
            vm.confirmR(vm.$t('Are you sure you want to make this Visible to Client?'), true, false, function (result) {

            },function () {
                vm.cancelAction();
            });
        },cancelAction() {
            (this.FormSchema)[0].Data[3].value = ((this.FormSchema)[0].Data[3].value==true) ? false: true;
        },


    },
    

});
</script>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>
