<template>
  <section>
    <loader :is-visible="isLoading"></loader>
    <div class="container-fluid mt-2">
      <div>
        <div id="checkForZone" class="row mt-0.5">
          <div v-if="mapType == 'Google' && config.isShowSearchBox == true" class="col-lg-5 col-sm-12">
            <div class="form-group d-flex">
              <div class="input-group customsearchbox divcityshown">
                <input type="text" id="pac-input" v-if="config.isShowSearchBox == true" maxlength="50"
                  placeholder="Enter Your Location or Zip Code" class="form-control">
              </div>
            </div>
          </div>
          <div class="col-lg-3 col-sm-12">
            <div class="form-group" v-if="config != undefined && config.isShowCurrentLocation == true">
              <a href="javascript:;" class="btn btn-secondary" @click="CurrentLocation">Use my location</a>
              &nbsp;&nbsp;&nbsp;OR
              <a href="javascript:;" class="btn btn-warning float-right" @click="ImportFilePopup"><i
                  class="fa fa-upload mr-2"></i>Click To import Geo JSON File.</a>
            </div>
          </div>

        </div>

        <div class="search">
          <div class="map-result">
            <div class="map-actions">
              <span v-if="config != undefined && config.isShowGetDirection == true" class="save legends_low"><a
                  href="javascript:;" v-if="config.isShowGetDirection == true"
                  :class="{ 'mp-active': showSetting == false, 'mg-success': showSetting == true }" title="GetDirection"
                  @click="GetDirection"><em class="fa fa-map-marker"></em></a></span>&nbsp;
              <span v-if="config != undefined && config.isShowLocator == true" class="save legends_low"><a
                  href="javascript:;" v-if="config.isShowLocator == true"
                  :class="{ 'mp-active': showSetting == false, 'mg-success': showSetting == true }"
                  title="View Locator on map" @click="ViewLocatorOnMap"><em
                    class="fa fa-map-marker"></em></a></span>&nbsp;
              <span v-if="config != undefined && config.isShowEtaMap == true" class="edit legends_profile"><a
                  href="javascript:;" v-if="config.isShowEtaMap == true" v-on:click="LoadLocatorDataonMap('locator')"
                  title="ETA Map"><em class="fa fa-map"></em></a></span>
              <span v-if="config != undefined && config.isShowEtaPipeLines == true" class="edit legends_high esri_prof">
                <a href="javascript:;" title="UtilityMap"><i class="fa fa-map-pin" aria-hidden="true"></i></a>
                <div class="dropdown">
                  <a href="javascript:;" title="UtilityMap"><i class="fa fa-map-pin" aria-hidden="true"></i></a>
                  <div class="dropdown-menu esri_prof_data" style="display: block;">
                    <div class="w-100 bg-grey">
                      <div class="custom-control custom-checkbox custom-control-inline w-100">
                        <input id="customcheckboxselectallUtilityMap" type="checkbox" value=""
                          class="custom-control-input dynamic customcheckboxselectall" />
                        <label for="customcheckboxselectallUtilityMap" type="checkbox" value=""
                          class="custom-control-label pt-1">SelectAll</label>
                      </div>
                    </div>
                    <div class="w-100 mb-2">
                      <a class="dropdown-item p-0" href="javascript:;" v-for="(item, index) in UtilityMapData"
                        :key="index">
                        <div class="custom-control custom-checkbox custom-control-inline w-100">
                          <input type="checkbox" class="custom-control-input dynamic customcheckboxUtilityMap" />
                          <label type="checkbox" class="custom-control-label pt-1"><label class="font-weight-bold">{{
                            item.LAYER_NAME }}</label></label>
                        </div>
                      </a>
                    </div>
                    <div><a href="javascript:;" title="Go" @click="ShowUtilityMap"
                        class="btn btn-primary d-block text-white py-2">Go<i
                          class="fa fa-angle-double-right pl-2"></i></a></div>
                  </div>
                </div>
              </span>
            </div>
            <div class="gmnoprint" @click="OpenSaveZone" v-if="showSaveZone" id="customSaveButton"
              style="position: absolute; right: 0px;z-index:9999999; top: 0px;">
              <div title="Save Zone"
                style="background-color: rgb(40, 167, 69); color: rgb(255, 255, 255); border-style: solid; border-width: 1px; border-color: rgb(204, 204, 204); margin-top: 4px; margin-left: -6px; cursor: pointer; text-align: center;">
                <div style="font-family: Arial, sans-serif; font-size: 14px; padding: 0.6rem 0.8rem;"><i
                    class="fa fa-save pr-2"></i>Save Zone</div>
              </div>
            </div>
            <!-- <div title="Delete Selected Shape" @click="DeleteFeature" v-if="showDeleteFeature" style="background-color: rgb(220, 53, 69); color: rgb(255, 255, 255); border-style: solid; border-width: 1px; border-color: rgb(204, 204, 204); margin-top: 4px; margin-left: -6px; cursor: pointer; text-align: center;"><div style="font-family: Arial, sans-serif; font-size: 14px; padding: 0.6rem 0.8rem;"><i class="fa fa-trash pr-2"></i>Delete Selected Shape</div></div> -->
            <div @click="DeleteFeature" v-if="showDeleteFeature" class="gmnoprint" id="customSaveButton"
              style="position: absolute; right: 1670px;z-index:999999; top: 0px;">
              <div title="Delete Save Zone"
                style="left: -121px; background-color: rgb(240, 4, 4); color: rgb(255, 255, 255); border-style: solid; border-width: 1px; border-color: rgb(204, 204, 204); margin-top: 4px;margin-right:122px; margin-left: -6px; cursor: pointer; text-align: center;">
                <div style="font-family: Arial, sans-serif; font-size: 14px; padding: 0.6rem 0.8rem;"><i
                    class="fa fa-trash pr-2"></i>Delete Selected Shape</div>
              </div>
            </div>
            <div class="map">

              <div :id="mapContainer" class="map_canvas map_ipadcontrol"
                v-bind:style="{ 'min-height': mapHeight == undefined ? '500px' : mapHeight, 'min-width': mapWidth == undefined ? '100%' : mapWidth }">
              </div>
              <div class="w-100 float-left" style="position: absolute; top: 0px;" v-cloak>
                <a class="showduration anchideduration" title="Hide" v-if="showSetting == false"
                  @click="ShowTravelMode">Hide</a>
                <a class="showduration ancshowduration" title="Show Setting" v-if="showSetting == true"
                  @click="ShowTravelMode">Show Setting</a>
                <div class="position-absolute float-left my24 mx24 bg-gray-faint round settingPopup d-none"
                  style="z-index:999999;">
                  <div id="params">
                    <div class="row float-left">
                      <div class="col float-left text-dark px-2">
                        <span class="timelegend min10"></span>
                        Minutes10
                      </div>
                      <div class="col float-left text-dark px-2">
                        <span class="timelegend min20"></span>
                        Minutes20
                      </div>
                      <div class="col float-left text-dark px-2">
                        <span class="timelegend min30"></span>
                        Minutes30
                      </div>
                    </div>
                    <h4 class="txt-m font-weight-bold">Choose Travel Mode:</h4>
                    <div class="mr12 toggle-group text-center">
                      <label class="toggle-container">
                        <input name="profile" type="radio" value="foot-walking" class="dynamic" />
                        <div class="toggle toggle--active-null toggle--null">Walking</div>
                      </label>
                      <label class="toggle-container">
                        <input name="profile" type="radio" value="cycling-regular" class="dynamic" />
                        <div class="toggle toggle--active-null toggle--null">Cycling</div>
                      </label>
                      <label class="toggle-container">
                        <input name="profile" type="radio" value="driving-car" checked="checked" class="dynamic" />
                        <div class="toggle toggle--active-null toggle--null">Driving</div>
                      </label>
                    </div>
                    <h4 class="txt-m font-weight-bold">Choose Maximum Duration:</h4>
                    <div class="w-100 float-left" style="margin-bottom:15px;">
                      <div class="w-30 float-left">
                        <label class="switch">
                          <input type="checkbox" name="duration" :value="time" @onChange="TravelMode(time)"
                            checked="checked" class="dynamic" />
                          <span class="sliderchk round"></span>
                        </label>
                        <div class="fs_textname ">Minutes10</div>
                      </div>
                      <div class="w-30 float-left">
                        <label class="switch">
                          <input type="checkbox" name="duration" :value="time * 2" checked="checked"
                            @onChange="TravelMode(time * 2)" class="dynamic" />
                          <span class="sliderchk round"></span>
                        </label>
                        <div class="fs_textname">Minutes20</div>
                      </div>
                      <div class="w-30 float-left">
                        <label class="switch">
                          <input type="checkbox" name="duration" checked="checked" @onChange="TravelMode(time * 3)"
                            :value="time * 3" class="dynamic" />
                          <span class="sliderchk round"></span>
                        </label>
                        <div class="fs_textname">Minutes30</div>
                      </div>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


  </section>
</template>

<script>
import mapboxgl from "mapbox-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import L from "leaflet";
import 'leaflet/dist/leaflet.css';
import MapboxDraw from "@mapbox/mapbox-gl-draw";
import '@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css'
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
import "leaflet.markercluster";
import "leaflet-draw"
import "leaflet-draw/dist/leaflet.draw.css";
import { GeoSearchControl, OpenStreetMapProvider } from 'leaflet-geosearch';
import 'leaflet-geosearch/dist/geosearch.css';
import DataServices from "@/services/DataService";
import "leaflet-routing-machine";
import "leaflet-routing-machine/dist/leaflet-routing-machine.css";
import { loadModules } from "esri-loader"
export default {
  components: {

  },
  props: {
    mapType: {
      required: true,
    },
    Data: {
      required: true,
    },
    isShowDrawButton: {
      required: true,
      type: String
    },
    config: {
      required: false
    },
    mapWidth: {
      required: false
    },
    mapHeight: {
      required: false
    },
    latLng: {
      required: false
    },
    mapContainer: {
      required: true
    },
    selectedUser: {
      required: true
    },
    UtilityMapData: {
      required: true
    },
    editZone: {
      required: true
    },
    zoneId: {
      required: true
    }
  },
  data: function () {
    return {
      isLoading: false,
      zoneEditData: [],
      showDeleteFeature: false,
      showSaveZonePopUp: false,
      showSaveZone: false,
      showAddZone: false,
      time: 600,
      colors: '',
      showSetting: null,
      address: [],
      UserName: null,
      UserEmailId: null,
      imageMarker: [],
      mapLayer: [],
      map: null,
      locations: [],
      locationPriority: [],
      locationname: [],
      ticketNumberList: [],
      LocationwithPriority: null,
      markers: [],
      mapZoom: false,
      mapMarker: [],
      markerCluster: null,
      startingPoint: null,
      showSettingPopup: false,
      lngLatData: [],
      fullAddress: [],
      leafLetReferenceNumber: [],
      polygonData: [],
      polygonNewShapeId: null,
      zoneData: [],
      drawingManager: null,
      currentSelectedShape: null,
      myPolygon: null,
      editableLayers: null,
      showHideEtaMap: false,
      showImportFilePopup: false,
      emailString: "",
      ZoneType: ""
    };
  },
  watch: {
    Data: {
      handler: function () {
        setTimeout(() => {
          this.ShowLocationOnGoogleMap()
          if (this.mapType == 'LeafLet') {
            this.initLeafletMap(this.Data);
          }
        }, 500);
      },
      deep: true
    },
    config: {
      handler: function () {
        this.ShowDrawingManager();
      },
      deep: true
    },
    selectedUser: {
      handler: function () {
        this.ViewLocatorOnMap(this.selectedUser);
      },
      deep: true
    },
    latLng: {
      handler: function () {
        this.ViewOnMap();
      }
    }
  },
  mounted: function () {
    if (this.mapType == "Google") {
      setTimeout(() => {
        // this.ShowMap1();
      }, 2000);
    } else if (this.mapType == "MapBox") {

      this.initMapBox(
        this.Data,
        "pk.eyJ1IjoiaGFycmlzbHVtZW4iLCJhIjoiY2s2dWtrMzJ3MDk2NTNncXMxMjlxa29mcCJ9.riRtpclzr9XfExHhclre8A"
      );

    } else if (this.mapType == "Esri") {
      setTimeout(() => {
        this.initLeafletMap(
          this.Data
        );
      }, 2000);
    } else if (this.mapType == "LeafLet" || this.mapType == "Esri") {
      setTimeout(() => {
        this.initLeafletMap(
          this.Data
        );
      }, 2000);
    }
  },
  created: function () {
    this.UserName = this.GetUserInfo.Name;
    this.UserEmailId = this.GetUserInfo.Email;
    if (this.myPolygon != undefined && this.myPolygon != null) {
      this.myPolygon.setMap(null);
    }
    let GoogleMapScript = document.createElement("script");
    GoogleMapScript.setAttribute(
      "src",
      "https://maps.googleapis.com/maps/api/js?key=AIzaSyAJdx4gmhGKWjPTBBVk5riFgpbE0lGS-xM&callback=initAutocompletes&libraries=geometry,drawing,places&v=weekly"
    );
    document.head.appendChild(GoogleMapScript);

    let cluserJsUrl = document.createElement("script");
    cluserJsUrl.setAttribute(
      "src",
      "https://unpkg.com/@google/markerclustererplus@4.0.1/dist/markerclustererplus.min.js"
    );
    document.head.appendChild(cluserJsUrl);

    let FontAwesomeMarker = document.createElement("script");
    FontAwesomeMarker.setAttribute(
      "src",
      "~/Scripts/FsmMaps/fontawesome-markers.min.js"
    );
    document.head.appendChild(FontAwesomeMarker);

    this.ShowLocationOnGoogleMap();
    this.MapExtendLocation();



  },
  methods: {
    MapExtendLocation: function () {
      var vm = this;
      if (vm.mapType == 'Google') {
        let Coords1 = [];
        vm.Data[0].mapExtendLatLong.forEach(function (item) {
          Coords1.push(new google.maps.LatLng(item[1], item[0]));
        });
        var bounds = new google.maps.LatLngBounds();
        var ars = Coords1;
        for (var i = 0, l = ars.length; i < l; i++) {
          bounds.extend(ars[i]);
        }
        vm.map.fitBounds(bounds)
      }
      else if (vm.mapType == 'MapBox') {
        let Coords = [];
        vm.Data[0].mapExtendLatLong.forEach(function (item) {

          Coords.push(item);
        });
        vm.map.fitBounds(Coords);
      }
      else {
        let Coords = [];
        vm.Data[0].mapExtendLatLong.forEach(function (item) {
          Coords.push([item[1], item[0]]);
        });
        var polygon = L.polygon(Coords);
        vm.map.fitBounds(polygon.getBounds());
      }
    },

    ImportFilePopup: function () {
      var vm = this;
      vm.showImportFilePopup = true;
    },
    initAutocompletes: function () {
      var vm = this;
      const input = document.getElementById("pac-input");
      const searchBox = new google.maps.places.SearchBox(input);
      vm.map.addListener("bounds_changed", () => {
        searchBox.setBounds(vm.map.getBounds());
      });
      google.maps.event.addListener(searchBox, 'places_changed', function () {
        //searchBox.set('map', null);


        var places = searchBox.getPlaces();

        var bounds = new google.maps.LatLngBounds();
        var i, place;
        for (i = 0; place = places[i]; i++) {
          (function (place) {
            bounds.extend(place.geometry.location);
          }(place));
        }
        vm.map.fitBounds(bounds);
        searchBox.set('map', vm.map);
        vm.map.setZoom(Math.min(vm.map.getZoom(), 12));
      });
    },
    CurrentLocation: function () {
      var vm = this;
      if (navigator.geolocation) {
        if (true)
          navigator.geolocation.getCurrentPosition(vm.GetCurrentLocation, vm.geoError);
        else
          navigator.geolocation.getCurrentPosition(showPosition, geoError);
      } else {
        alert("Geolocation is not supported by this browser.");
      }
    },
    GetCurrentLocation: function (position) {
      var vm = this;
      if (position) {
        var latitude = position.coords.latitude;
        var longitude = position.coords.longitude;
        var LatLng = new google.maps.LatLng(latitude, longitude);
        vm.map.setCenter(LatLng);
        vm.map.setZoom(18);
      }
    },
    SaveMapExtent: function () {
      var vm = this;
      vm.$emit("SaveMapExtent", vm.fullAddress, vm.mapType);
    },
    ShowUtilityMap: function () {
      var vm = this;
      var coordinates = JSON.parse(localStorage.getItem("coordinates"));
      if (coordinates == null) {
        coordinates = {
          "x": "43.133971",
          "x1": "43.132812",
          "y": "-76.088009",
          "y1": "-76.087183"
        };
      }
      loadModules(['esri/Map', 'esri/views/MapView', 'esri/layers/FeatureLayer', 'esri/Graphic', 'esri/layers/GraphicsLayer'])
        .then(([Map, MapView, FeatureLayer, Graphic, GraphicsLayer]) => {
          var map = new Map({
            basemap: "hybrid"
          });
          var view = new MapView({
            container: "utilitymap",
            map: vm.map,
            extent: {
              // autocasts as new Extent()
              xmin: -13856051.7748509,
              ymin: 2821931.08215458,
              xmax: -7488766.97004404,
              ymax: 6364777.09974285,
              spatialReference: 102100
            }
          });
          if (layerUrl != '' && layerUrl != null) {
            Colors = {};
            Colors.names = {
              aqua: "#00ffff",
              azure: "#f0ffff",
              beige: "#f5f5dc",
              black: "#000000",
              blue: "#0000ff",
              brown: "#a52a2a",
              cyan: "#00ffff",
              darkblue: "#00008b",
              darkcyan: "#008b8b",
              darkgrey: "#a9a9a9",
              darkgreen: "#006400",
              darkkhaki: "#bdb76b",
              darkmagenta: "#8b008b",
              darkolivegreen: "#556b2f",
              darkorange: "#ff8c00",
              darkorchid: "#9932cc",
              darkred: "#8b0000",
              darksalmon: "#e9967a",
              darkviolet: "#9400d3",
              fuchsia: "#ff00ff",
              gold: "#ffd700",
              green: "#008000",
              indigo: "#4b0082",
              khaki: "#f0e68c",
              lightblue: "#add8e6",
              lightcyan: "#e0ffff",
              lightgreen: "#90ee90",
              lightgrey: "#d3d3d3",
              lightpink: "#ffb6c1",
              lightyellow: "#ffffe0",
              lime: "#00ff00",
              magenta: "#ff00ff",
              maroon: "#800000",
              navy: "#000080",
              olive: "#808000",
              orange: "#ffa500",
              pink: "#ffc0cb",
              purple: "#800080",
              violet: "#800080",
              red: "#ff0000",
              silver: "#c0c0c0",
              white: "#ffffff",
              yellow: "#ffff00"
            };
            Colors.random = function () {
              var result;
              var count = 0;
              for (var prop in this.names)
                if (Math.random() < 1 / ++count)
                  result = prop;
              return { name: result, rgb: this.names[result] };
            };
            // Define a unique value renderer and symbols

            String(layerUrl).split(',').forEach(function (item) {
              /********************
           * Add feature layer
           ********************/
              var layerColor = String(item).split('^')[1];
              if (layerColor == '' || layerColor == null || layerColor == 'null' || typeof (layerColor) == 'undefined') {
                layerColor = String(Colors.random().rgb).toUpperCase();
              }
              const trailsRenderer = {
                type: "simple",
                symbol: {
                  color: layerColor,
                  type: "simple-line",
                  style: "solid"
                },

                visualVariables: [
                  {
                    type: "size",
                    field: "ELEV_GAIN",
                    minDataValue: 0,
                    maxDataValue: 2300,
                    minSize: "3px",
                    maxSize: "7px"
                  }
                ]
              };
              var url = String(item).split('^')[0];
              //trailsRenderer.symbol.color = String(Colors.random()).toUpperCase();
              map.add(new FeatureLayer({
                url: url,
                renderer: trailsRenderer,
              }));
            });

          }
          var view = new MapView({
            container: "utilitymap",
            map: map,
            basemap: "hybrid",
            center: [coordinates.y, coordinates.x],
            zoom: 17
          });
          var graphicsLayer = new GraphicsLayer();
          map.add(graphicsLayer);
          var polygon = null;
          if (coordinates != undefined && coordinates != null) {
            polygon = [[coordinates.y1, coordinates.x1],
            [coordinates.y1, coordinates.x],
            [coordinates.y, coordinates.x],
            [coordinates.y, coordinates.x1]];
          }
          // Create a polygon geometry
          var polygon = {
            type: "polygon",
            rings: polygon
          };
          var simpleFillSymbol = {
            type: "simple-fill",
            color: [255, 0, 0, 0.35],  // orange, opacity 80%
            outline: {
              color: [255, 0, 0],
              width: 1
            }
          };
          var polygonGraphic = new Graphic({
            geometry: polygon,
            symbol: simpleFillSymbol
          });
          graphicsLayer.add(polygonGraphic);
        })
        .catch(err => {
          // handle any errors
          console.error(err);
        });
    },
    GetDirection: function () {
      var vm = this;
      if (navigator.geolocation) {
        if (true)
          navigator.geolocation.getCurrentPosition(vm.showRoutePosition, vm.geoError);
        else
          navigator.geolocation.getCurrentPosition(showPosition, geoError);
      } else {
        alert("Geolocation is not supported by this browser.");
      }
    },
    geoError: function () {
      //showPosition();
    },
    showRoutePosition: function (position) {
      var vm = this;
      if (position) {
        //RemoveAllMapImageMarker();
        var latitude = position.coords.latitude;
        var longitude = position.coords.longitude;
        var StartPoint = new google.maps.LatLng(latitude, longitude),
          EndPoint = new google.maps.LatLng(vm.Data[0].pinLatLong.latitude.split(',')[0], vm.Data[0].pinLatLong.latitude.split(',')[1])
        var mapicon = localStorage.getItem("UserImage");
        if (String(mapicon).indexOf('blob') <= 0) {
          mapicon = 'https://app.talygen.com/Content/images/Black/user.png'
        }
        var myLatLng = { lat: latitude, lng: longitude };
        if (vm.mapType == "Google") {
          var icon = {
            url: mapicon, // url
            scaledSize: new google.maps.Size(40, 40), // scaled size
            origin: new google.maps.Point(0, 0), // origin
            anchor: new google.maps.Point(0, 0) // anchor
          };
          var marker = new google.maps.Marker({
            position: myLatLng,
            icon: icon,
            map: vm.map,
          });
          var infowindow = new google.maps.InfoWindow({
            content: "<div style='padding:8px;'><img style='float:left;' src='" + mapicon + "' class='marker' /> <div style='display:block;width: 270px;'>@loggedUserInfo.Name </br>@loggedUserInfo.Email</div></div>"
          });
          marker.addListener('click', function () {
            infowindow.open(vm.map, this);
          });
          vm.imageMarker.push(marker);
          var lineSymbol = {
            path: google.maps.SymbolPath.CIRCLE,
            fillOpacity: 1,
            scale: 3
          };
          var polylineDotted = {
            strokeColor: '#ff0000',
            strokeOpacity: 0,
            fillOpacity: 0,
            icons: [{
              icon: lineSymbol,
              offset: '0',
              repeat: '4px'
            }],
          };
          var rendererOptions = {
            map: vm.map,
            suppressMarkers: false,
            polylineOptions: polylineDotted
          };
          var directionsService = new google.maps.DirectionsService();
          var directionsRenderer = new google.maps.DirectionsRenderer(rendererOptions);
          var start = new google.maps.LatLng(latitude, longitude);
          //var end = new google.maps.LatLng(lat, lng);
          directionsRenderer.setMap(vm.map);
          calculateAndDisplayRoute(directionsService, directionsRenderer);
          function calculateAndDisplayRoute(directionsService, directionsRenderer) {
            var selectedMode = 'DRIVING';
            directionsService.route({
              origin: StartPoint,
              destination: EndPoint,
              travelMode: google.maps.TravelMode[selectedMode]
            }, function (response, status) {
              if (status == 'OK') {
                directionsRenderer.setDirections(response);
                var iwContent = '<span style="font-weight:bold;">Distance: </span>' + response['routes'][0].legs[0].distance.text + '<br /><span style="font-weight:bold;">Time: </span>' + response['routes'][0].legs[0].duration.text;
                var infoWindows = new google.maps.InfoWindow({
                  content: iwContent
                });
                google.maps.event.addListener(polylineDotted, 'click', function (event) {
                  //debugger;
                  infoWindows.setPosition(event.latLng);
                  infoWindows.open(map);
                });
                google.maps.event.addListener(map, 'click', function () {
                  infoWindows.close();
                });
              } else {
                window.alert('Directions request failed due to ' + status + ". Route exceeds maximum distance limitation.");
              }
            });
          }
        }
        else if (vm.mapType == "MapBox") {
          var lngLat = {
            lon: parseFloat(myLatLng.lng, 10),
            lat: parseFloat(myLatLng.lat, 10)
          };
          var html = '<img src="' + mapicon + '" style="width:100px;height:100px;" />';
          var popup = new mapboxgl.Popup({ offset: 25 }).setHTML(html);
          var el = document.createElement('div');
          el.className = 'marker';
          el.style.backgroundImage = 'url(' + mapicon + ')';
          el.style.backgroundSize = 'cover';
          el.style.width = '40px';
          el.style.height = '40px';
          var marker = new mapboxgl.Marker(el)
            .setPopup(popup)
            .setLngLat(lngLat)
            .addTo(vm.map);
          vm.imageMarker.push(marker);
          var url = "https://api.mapbox.com/directions/v5/mapbox/driving/" + longitude + "," + latitude + ";" + vm.Data[0].pinLatLong.latitude.split(',')[1] + "," + vm.Data[0].pinLatLong.latitude.split(',')[0] + "?steps=true&geometries=geojson&access_token=" + mapboxgl.accessToken;
          // Add starting point to the map
          if (!vm.map.getLayer('point')) {
            vm.map.addLayer({
              id: 'point',
              type: 'circle',
              source: {
                type: 'geojson',
                data: {
                  type: 'FeatureCollection',
                  features: [{
                    type: 'Feature',
                    properties: {},
                    geometry: {
                      type: 'Point',
                      coordinates: start
                    }
                  }
                  ]
                }
              },
              paint: {
                'circle-radius': 10,
                'circle-color': '#3887be'
              }
            });
          }
          var req = new XMLHttpRequest();
          req.open('GET', url, true);
          req.onload = function () {
            var json = JSON.parse(req.response);
            if (typeof (json.code) != "" && json.code != "InvalidInput") {
              var data = json.routes[0];
              var route = data.geometry.coordinates;
              var geojson = {
                type: 'Feature',
                properties: {
                  distance: data.distance,
                  duration: data.duration,
                },
                geometry: {
                  type: 'LineString',
                  coordinates: route
                }
              };
              // if the route already exists on the map, reset it using setData
              if (vm.map.getSource('route')) {
                vm.map.getSource('route').setData(geojson);
              } else { // otherwise, make a new request
                vm.map.addLayer({
                  id: 'route',
                  type: 'line',
                  source: {
                    type: 'geojson',
                    data: {
                      type: 'Feature',
                      properties: {},
                      geometry: {
                        type: 'LineString',
                        coordinates: geojson
                      }
                    }
                  },
                  layout: {
                    'line-join': 'round',
                    'line-cap': 'round'
                  },
                  paint: {
                    'line-color': '#3887be',
                    'line-width': 5,
                    'line-opacity': 0.75
                  }
                });
                vm.map.on('click', 'route', function (e) {
                  var features = vm.map.queryRenderedFeatures(e.point, {
                    layers: ['route']
                  });
                  //debugger;
                  var description = e.features[0].properties;
                  var distance = description.distance;
                  distance = parseFloat(distance * 0.000621371192, 10).toFixed(2);
                  var iwContent = '<span style="font-weight:bold;">Distance: </span>' + distance + ' mi<br /><span style="font-weight:bold;">Time: </span>' + timeConvert(description.duration);
                  new mapboxgl.Popup()
                    .setLngLat(e.lngLat)
                    .setHTML(iwContent)
                    .addTo(vm.map);
                });
              }
            } else {
              try {
                ShowMsgWithMsg("Route exceeds maximum distance limitation.", "failure");
              } catch (ex) {
                alert("Route exceeds maximum distance limitation.");
              }
            }
            // add turn instructions here at the end
          };
          req.send();
        }
        else {
          var markerIcon = L.icon({
            iconUrl: mapicon,
            iconSize: [40, 40], // size of the icon
          });
          var popup = L.popup().setContent("<img src=" + mapicon + " />");
          L.marker([latitude, longitude], { icon: markerIcon }).bindPopup(popup).openPopup().addTo(vm.map);
          var control = L.Routing.control({
            waypoints: [
              L.latLng(latitude, longitude),
              L.latLng(vm.Data[0].pinLatLong.latitude.split(',')[0], vm.Data[0].pinLatLong.latitude.split(',')[1])
            ],
            routeWhileDragging: false,
            createMarker: function () { return null; }
          }).addTo(vm.map);
          control.on('routesfound', function (e) {
            var routes = e.routes;
            var summary = routes[0].summary;
            // alert time and distance in km and minutes
            //alert('Total distance is ' + summary.totalDistance / 1000 + ' km and total time is ' + Math.round(summary.totalTime % 3600 / 60) + ' minutes');
            //setTimeout($(".leaflet-routing-container").hide(), 1000);
          });
        }
      }
    },
