    ViewLocatorOnMap: function (id) {
      var vm = this;
      var appendingUrl
      vm.isLoading = true;
      if (vm.selectedUser != undefined) {
        appendingUrl = `selectedUser=${id}&reqFrom=`
      }
      else {
        appendingUrl = `reqFrom=ETAMAP`
      }

      DataServices.TrackByUserId(appendingUrl).then(function (response) {
        if (response.data.data != null && response.data.data.length > 0) {
          var userTrackData = response.data.data;
          vm.ShowUserOnMap(userTrackData, vm.map);
          vm.isLoading = false;
        }
        else {
          vm.ShowAlert(vm.$t('CurrentLocationNotFound'), 'warning', true, vm.$t("Alert"));
          vm.isLoading = false;
        }
      })
    },
    ShowUserOnMap: function (userTrackData, map) {
      var vm = this;
      var userLngLatArr = [];
      $.each(userTrackData, function (index, item) {
        var mapicon = item.IMAGE_PATH;
        var myLatLng = {
          lat: parseFloat(item.LATITUDE, 10),
          lng: parseFloat(item.LONGITUDE, 10)
        };
        userLngLatArr.push([myLatLng.lat, myLatLng.lng]);
        var myCenter = new google.maps.LatLng(myLatLng.lat, myLatLng.lng);
        if (String(mapicon).indexOf('blob') <= 0) {
          mapicon = '/Content/images/user_avtar.png'
        }
        var html = '<div class="w-100 float-left popupcontent" style="padding:0px 6px 0px 0px;">';
        html += '<img src="' + mapicon + '" class="float-left" style="width:50px;height:50px; border-radius: 100%;border: 3px solid #f2f2f2;" />';
        html += '<div style="padding-left:8px; padding-top:5px;width:80%;" class="float-left">';
        html += "<p style='margin-bottom: 2;'><b style='font-weight:bold;'>Name: </b>" + item.NAME + " </p>";
        html += "<p style='margin-bottom: 2;'><b style='font-weight:bold;'>Department: </b>" + item.DEPARTMENT_NAME + " </p></div>";
        if (item.TRACK_ID > 0) {
          html += "<p style='margin-bottom:2px;' class='px-2 float-left'><b style='font-weight:bold;'>" + item.NAME + " </b> is going to ticket number: " + item.TICKET_NUMBER + " </p>";
        }
        html += "</div>";
        if (vm.mapType == "Google") {
          var icon = {
            url: mapicon, // url
            scaledSize: new google.maps.Size(40, 40), // scaled size
            origin: new google.maps.Point(0, 0), // origin
            anchor: new google.maps.Point(0, 0) // anchor
          };
          var marker = new google.maps.Marker({
            position: myLatLng,
            icon: icon,
            map: vm.map,
          });
          vm.imageMarker.push(marker);
          //vm.map.panTo(myCenter);
          var infowindow = new google.maps.InfoWindow({
            content: html
          });
          try {
            marker.addListener('click', function () {
              infowindow.open(vm.map, this);
            });
          } catch (e) { console.log(e) }
        }
        else if (vm.mapType == "MapBox") {
          var lngLat = {
            lon: parseFloat(myLatLng.lng, 10),
            lat: parseFloat(myLatLng.lat, 10)
          };
          var popup = new mapboxgl.Popup({ offset: 25 }).setHTML(html);
          var el = document.createElement('div');
          el.className = 'marker';
          el.style.backgroundImage = 'url(' + mapicon + ')';
          el.style.backgroundSize = 'cover';
          el.style.width = '50px';
          el.style.height = '50px';
          var marker = new mapboxgl.Marker(el)
            .setPopup(popup)
            .setLngLat(lngLat)
            .addTo(vm.map);
          vm.map.flyTo({
            center: [lngLat.lon, lngLat.lat],
            essential: true // this animation is considered essential with respect to prefers-reduced-motion
          });
          vm.imageMarker.push(marker);
        }
        else {
          var markerIcon = L.icon({
            iconUrl: mapicon,
            iconSize: [40, 40], // size of the icon
          });
          var popup = L.popup().setContent(html);
          var marker = L.marker([myLatLng.lat, myLatLng.lng], { icon: markerIcon }).bindPopup(popup).openPopup().addTo(vm.map);
          vm.imageMarker.push(marker);
          vm.map.fitBounds(userLngLatArr)
        }
      });
    },
