    ShowLocationOnGoogleMap: function () {


      if (this.mapType == "Google") {
        var vm = this;
        var marker;
        var lat = null;
        const mark = [];
        var lng = null;
        var mapicon;
        var infowindow = new google.maps.InfoWindow();
        vm.map = null;
        vm.showLoader = true;
        vm.map = new google.maps.Map(document.getElementById(vm.mapContainer), {
          zoom: 7,
          zoomControl: true,
          mapTypeControlOptions: {
            style: google.maps.MapTypeControlStyle.HORIZONTAL_BAR,
            position: google.maps.ControlPosition.BOTTOM_RIGHT
          },
          zoomControlOptions: {
            position: google.maps.ControlPosition.RIGHT_CENTER,
          },
          center: new google.maps.LatLng(30.7333, 76.7794),
          mapTypeId: google.maps.MapTypeId.ROADMAP,
        });
        if (vm.config != undefined && vm.config.isShowDrawButton == true) {
          vm.drawingManager = new google.maps.drawing.DrawingManager({
            drawingControl: true,
            // markerOptions: {
            //     draggable: true
            // },
            drawingControlOptions: {
              position: google.maps.ControlPosition.RIGHT_CENTER,
              drawingModes: ['polygon']
            },
            polylineOptions: {
              editable: true
            },
          })
          vm.drawingManager.setMap(vm.map);
          google.maps.event.addListener(vm.drawingManager, 'overlaycomplete', function (e, x, y) {
            var newShape = e.overlay;
            vm.currentSelectedShape = newShape;
            vm.GetGoogleAddressToDraw(e);
          })
        }
        if ((this.mapType = "Google")) {
          $.each(vm.Data, function (key, value) {
            if (value.pinLatLong.latitude != null) {
              lat = value.pinLatLong.latitude.split(',')[0]
              lng = value.pinLatLong.latitude.split(',')[1]
            }
            if (value.priority == 'Critical') {
              mapicon = 'https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png'
            }
            else if (value.priority == 'High') {
              mapicon = 'https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_high.png'
            }
            else if (value.priority == 'Medium') {
              mapicon = 'https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_medium.png'
            }
            else {
              mapicon = 'https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_low.png'
            }
            let popup = '<div id="content">' +
              '<div class="map-pop-header ' + value.priority + '" style="background-color:' + value.color_code + '">' +
              '<div id="siteNotice"></div>' + value.popupHeaderHtml + '</div>' +
              '<div id="bodyContent">' + value.popupBodyHtml +
              '<p class="view-details">' + value.popupFooterHtml + '</p></div></div>';
            marker = new google.maps.Marker({
              position: new google.maps.LatLng(
                lat,
                lng
              ),
              icon: mapicon,
              title: value.refValue,
              map: vm.map,
              address: popup
            });

            mark.push(marker);
            vm.markers.push(marker);
            let Coords = [];
            //Get Multiple Polygon
            if (vm.Data[key] != undefined) {
              vm.Data[key].polygons.forEach(function (polygon) {

                polygon.latLong.forEach(function (item) {
                  Coords.push(new google.maps.LatLng(item.longitude, item.latitude));
                });
                vm.myPolygon = new google.maps.Polygon({
                  paths: Coords,
                  draggable: false, // turn off if it gets annoying
                  editable: vm.config.isEditable == true ? true : false,
                  strokeColor: (typeof (polygon.strokeColor) != 'undefined') ? polygon.strokeColor : '#FF0000',
                  strokeOpacity: (typeof (polygon.strokeOpacity) != 'undefined') ? polygon.strokeOpacity : '#FF0000',
                  strokeWeight: (typeof (polygon.strokeWeight) != 'undefined') ? polygon.strokeWeight : '#FF0000',
                  fillColor: (typeof (polygon.fillColor) != 'undefined') ? polygon.fillColor : '#FF0000',
                  fillOpacity: ((typeof (polygon.opacity) != 'undefined') ? polygon.opacity : 0.35),
                  Id: vm.Data[key].refValue,
                  FormName: polygon.FormName,
                  Description: polygon.Description,
                  ZoneType: polygon.ZoneType,
                  locatorids: polygon.locatorids
                });
                vm.zoneEditData.push({
                  FormName: polygon.FormName,
                  Description: polygon.Description,
                  Refrence: 'View',
                  ZoneType: polygon.ZoneType,
                  YardEmails: polygon.YardEmails,
                  locatorids: polygon.locatorids
                })
                //vm.map.setCenter(Coords[1]);
                vm.myPolygon.setMap(vm.map);
                google.maps.event.addListener(vm.myPolygon, 'click', function (e, e1) {
                  vm.currentSelectedShape = this;
                  vm.currentSelectedShape.setOptions({ editable: true, ShapeId: vm.polygonNewShapeId })
                  vm.showDeleteFeature = true;
                  if (vm.mapContainer == 'zoneMapDiv') {
                    vm.showAddZone = true;
                  }
                });
              });
            }
            if (vm.mapContainer == 'zoneMapDiv' || vm.mapContainer == 'mapExtend') {
              var bounds = new google.maps.LatLngBounds();
              var ar = Coords;
              for (var i = 0, l = ar.length; i < l; i++) {
                bounds.extend(ar[i]);
              }
              vm.map.fitBounds(bounds)
            }

            google.maps.event.addListener(vm.myPolygon.getPath(), 'set_at', function (e, e1) {
              var curLatLng;
              let address = { FullAddress: '' };
              vm.lngLatData = [];
              if (vm.mapContainer == 'zoneMapDiv') {
                vm.polygonNewShapeId = new Date().getTime() + '_' + Math.floor(Math.random() * 1000);
                vm.myPolygon.setOptions({ ShapeId: vm.polygonNewShapeId })
                vm.showAddZone = true;
                curLatLng = vm.myPolygon.getPath().getArray();
                curLatLng.forEach(function (item, i) {
                  vm.lngLatData.push({
                    latitude: item.lat(),
                    longitude: item.lng(),
                  })
                })
                address = { FullAddress: '' };
                var point = new google.maps.LatLng(vm.lngLatData[0].Lat, vm.lngLatData[0].Lng);
                var geocoder = new google.maps.Geocoder();
                geocoder.geocode({ 'latLng': point },
                  function (results, status) {
                    address.FullAddress = results[0].formatted_address;
                  })
                vm.polygonData.push({
                  Id: vm.polygonNewShapeId,
                  latLong: vm.lngLatData,
                  CompleteAddress: address,
                  strokeOpacity: vm.myPolygon.strokeOpacity,
                  strokeWeight: vm.myPolygon.strokeWeight,
                  strokeColor: vm.myPolygon.strokeColor,
                  fillColor: vm.myPolygon.fillColor,
                  locatorids: ''
                })
                vm.fullAddress.push({ 'LatLng': vm.lngLatData, 'CompleteAddress': address })
              }
              else {
                vm.lngLatData = [];
                var curLatLng;
                curLatLng = vm.myPolygon.getPath().getArray();
                curLatLng.forEach(function (item, i) {
                  vm.lngLatData.push({
                    Lat: item.lat(),
                    Lng: item.lng(),
                  })
                })
                address = { FullAddress: '' };
                var point = new google.maps.LatLng(vm.lngLatData[0].Lat, vm.lngLatData[0].Lng);
                var geocoder = new google.maps.Geocoder();
                geocoder.geocode({ 'latLng': point },
                  function (results, status) {
                    address.FullAddress = results[0].formatted_address;
                  })
                vm.fullAddress.push({ 'LatLng': vm.lngLatData, 'CompleteAddress': address })
              }

            });
            google.maps.event.addListener(marker, "click", (function (marker, i) {
              return function () {
                infowindow.setContent(popup);
                infowindow.open(vm.map, marker);
                if (vm.mapContainer == "WorkOrderDetail" || vm.mapContainer == "detailMapDiv") {
                  infowindow.close();
                }
              };
            })(marker, key)

            );
