    // Initialize the Mapbox map
    initMapBox: function (obj, apikey, vueObj, indexMap) {
      var vm = this;
      var center = "";
      let zoom = 3;

      if (obj) {
        var item = $.grep(obj, function (element, index) {
          return (
            element.location_nw !== "" &&
            element.location_nw !== null &&
            typeof element.location_nw !== undefined &&
            element.location_nw !== ","
          );
        });
        item = item[0];
        if (item) {
          center = [
            parseFloat(String(item.location_nw).split(",")[1], 10),
            parseFloat(String(item.location_nw).split(",")[0], 10),
          ];
        }
      }

      vm.startingPoint = center;
      mapboxgl.accessToken = apikey;
      vm.map = new mapboxgl.Map({
        container: vm.mapContainer,
        style: "mapbox://styles/mapbox/streets-v11",
        zoom: zoom,
        zoomControl: true,
        scrollZoom: true,
        boxZoom: true
      });

      if (vm.config !== undefined && vm.config.isShowSearchBox) {
        vm.geocoder = new MapboxGeocoder({
          accessToken: apikey,
          mapboxgl: mapboxgl,
          marker: false,
          placeholder: 'Search for places in Barbados',
        });
        vm.map.addControl(vm.geocoder);
      }

      if (vm.config !== undefined && vm.config.isShowDrawButton) {
        vm.draw = new MapboxDraw({
          displayControlsDefault: false,
          controls: {
            line_string: false,
            polygon: false,
            point: true,
            trash: true
          }
        });
        vm.map.addControl(vm.draw);
        vm.map.on('draw.create', vm.GetMapBoxAddressToDraw);
        vm.map.on('draw.delete', vm.deleteMarker) 
        
        vm.navTopLeft = new mapboxgl.NavigationControl();
        vm.map.addControl(vm.navTopLeft, 'top-left');
      }

      vm.navBottomRight = new mapboxgl.NavigationControl();
      vm.map.addControl(vm.navBottomRight, 'bottom-right');



    },


