                    marker.getPopup().getElement().classList.add('d-none');
                  }
                  if (ancCtrl.length > 0 && localStorage.getItem("isMapView") == "true") {
                    var ctrl = ancCtrl.closest(".lists");
                    ctrl.addClass("alert-info");
                    setTimeout(function () {
                      ctrl.removeClass("alert-info");
                    }, 5000);
                  }
                  ancCtrl = $("a.ticketnumberlist[data-ticketnumber='" + ticketNumber + "']");
                  if (ancCtrl.length > 0 && localStorage.getItem("isMapView") == "false") {
                    ancCtrl = ancCtrl.closest("tr");
                    ancCtrl.addClass("alert-info");
                    setTimeout(function () {
