    SaveZoneData: function (objs) {
      var vm = this;
      let obj = {
        ZoneId: vm.zoneId == 0 ? objs.ZoneId : vm.zoneId,
        ResolverUserId: null,
        JsonData: {
          type: "FeatureCollection",
          features: []
        },
        ZoneName: objs.Name,
        description: objs.Description,
        ZoneType: objs.ZoneType == null ? vm.ZoneType : objs.ZoneType,
        YardEmails: objs.YardEmails == null ? vm.emailString : objs.YardEmails
      }
      vm.polygonData.forEach(function (item, i) {

        obj.JsonData.features.push({
          "type": "Feature",
          "geometry": {
            "type": "Polygon",
            "coordinates": [[]],
          },
          "properties": {
            "stroke": item.strokeWeight,
            "stroke-opacity": item.strokeOpacity,
            "stroke-width": item.strokeWeight,
            "fillColor": item.fillColor,
            "fill-opacity": item.fillOpacity,
            "locatorname": null,
            "locatorids": item.locatorids,
            "userids": null
          }
        })
        item.latLong.forEach(function (row, is) {
          if (obj.JsonData.features[i] != undefined) {
            obj.JsonData.features[i].geometry.coordinates[0].push([row.longitude, row.latitude]);
          }
        })
        if (obj.JsonData.features[i] != undefined) {
          obj.JsonData.features[i].geometry.coordinates[0].push([obj.JsonData.features[i].geometry.coordinates[0][0][0], obj.JsonData.features[i].geometry.coordinates[0][0][1]]);
        }
      })
      vm.zoneData.push(obj);
      var params = JSON.stringify(vm.zoneData);
      DataServices.UploadGeoJSON(params).then(function (response) {
        if (vm.zoneId == 0) {
          vm.ShowAlert("Zone Succesfully Added.", "success")
        }
        else {
          vm.ShowAlert("Zone Succesfully Updated.", "success")
        }
        vm.showSaveZonePopUp = false;
        vm.currentSelectedShape = null;
        vm.$emit("FetchData");
      })
    },
