                }
                else if (item.types[0] == 'administrative_area_level_1') {
                  address.AdministrativeAreaLevel1.LongName = item.long_name;
                  address.AdministrativeAreaLevel1.Name = item.short_name;
                }
                else if (item.types[0] == 'administrative_area_level_2') {
                  address.AdministrativeAreaLevel2.LongName = item.long_name;
                  address.AdministrativeAreaLevel2.Name = item.short_name;

                  address.State.LongName = item.long_name;
                  address.State.Name = item.short_name;
                }
                else if (item.types[0] == 'administrative_area_level_3') {
                  vm.address.push({
                    AdministrativeArea3: item.long_name
                  })
                  address.AdministrativeAreaLevel3.LongName = item.long_name;
                  address.AdministrativeAreaLevel3.Name = item.short_name;
                }
                else if (item.types[0] == 'locality') {
                  vm.address.push({
                    City: item.long_name
                  })
                  address.City.LongName = item.long_name;
                  address.City.Name = item.short_name;
                }
              })
              vm.fullAddress.push({ 'LatLng': vm.lngLatData, 'CompleteAddress': address })
              vm.$emit('GetAddress', vm.fullAddress);
            }
          });
      }
    }
  },
};
</script>
