<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle pt-2">
                            <h4 name="header" class="modal-title mt-1 pt-1">
                                {{ $t('Members') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>

                        <div class="listing">

                            <loader :is-visible="isLoading"></loader>

                            <div class="col-sm-12 col-md-12 col-lg-12 modal-body" id="divTeamMembers"
                                style="height: auto; min-height: 0px; max-height: none;">
                                <div class="col-lg-12 mb-3 p-0">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span>Other Employee(s) in Company:</span>
                                        </h5>
                                    </div>
                                </div>

                                <input v-model="searchUserName" @input="onSearchInput" class="form-control mb-3"
                                    id="inputEmployee" placeholder="Search by Employee Name" />

                                <div class="row">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left" id="divEmployee">

                                        <div class="col-lg-12 p-0 float-left">
                                            <div class="listing">
                                                <div class="table-responsive table-fix-header">
                                                    <table id="MemeberList"
                                                        class="table table-bordered dt-responsive nowrap mt-0">
                                                        <thead class="thead-bg">
                                                            <tr>
                                                                <th style="width:3% !important;" class="text-center">


                                                                    <div class="custom-control custom-checkbox">
                                                                        <input type="checkbox"
                                                                            class="addmember custom-control-input"
                                                                            id="chkAll_8" v-model='allSelected'
                                                                            @click="selectAll($event)"
                                                                            name="checkbox" />
                                                                        <label
                                                                            class="custom-control-label universal-custom-control-label pt-1"
                                                                            for="chkAll_8"></label>
                                                                    </div>


                                                                </th>
                                                                <th>
                                                                    <span>Name</span>
                                                                </th>
                                                                <th class="text-center">
                                                                    <span>Designation</span>
                                                                </th>
                                                                <th class="text-center">
                                                                    <span>Department</span>
                                                                </th>
                                                                <th class="text-center">
                                                                    <span>User Type</span>
                                                                </th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-if="filteredUserList.length == 0">
                                                                                <td colspan="5"
                                                                                    class="text-danger text-center no-record">
                                                                                    No record(s) found</td>
                                                                            </tr>
                                                            <tr class="teamstructurebase" v-for="item in filteredUserList" :key="item.user_id">
                                                                <td class="text-center">
                                                                    <div class="custom-control custom-checkbox">
                                                                        <input :id='item.user_id'
                                                                            class="custom-control-input" type="checkbox"
                                                                            name="checkbox" v-model="item.isSelected"
                                                                            :value="item.user_id"
                                                                            @change="select($event, item)">
                                                                        <label
                                                                            class="custom-control-label universal-custom-control-label pt-1"
                                                                            :for="item.user_id"></label>
                                                                    </div>
                                                                </td>
                                                                <td>
                                                                    <div class="org_manager_base">
                                                                        <div class="pull-left image">
                                                                            <img src="/Content/images/DefaultImages/avatar-icon.jpg"
                                                                                class="img-circle avatar"
                                                                                alt="Profile Image">
                                                                        </div>
                                                                    </div>
                                                                    <span style="line-height:3"> {{ item.USER_NAME
                                                                        }}</span>
                                                                    <input type="hidden" class="hdnEmpId"
                                                                        source="EmployeeTeam" value="80720">
                                                                </td>
                                                                <td class="text-center">
                                                                    <span>{{ item.designation_name }}</span>
                                                                </td>
                                                                <td class="text-center">
                                                                    <span>{{ item.department_name }}</span>
                                                                </td>
                                                                <td class="text-center">
                                                                    <span>{{ item.USER_TYPE_NAME }}</span>
                                                                </td>
                                                            </tr>

                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                        </div>
                                        <input type="hidden" id="hdnsecondaryid" value="">

                                    </div>
                                </div>

                                <div class="row flex-row-reverse mt-3">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:;" class="btn btn-success float-right mt-2"
                                    v-on:click="SavePrimaryMember()" title="" data-toggle-tooltip="tooltip"
                                    data-original-title="Save"><i class="fa fa-save pr-2"></i>Save</a>
                                           
                                        </div>
                               
                                </div>



                                
                            </div>



                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

import DataService from '../../services/DataService';

export default {
    props: {
        UserId: {
            type: Number
        }, type: {
            type: String
        }

    },
    data() {
        return {
            isLoading: false,
            userList: [],
            userIds: '',
            allSelected: false,
            searchUserName: '',

        }
    },
    watch: {
        userList: {
            deep: true,
            handler(oldlist, newList) {
                console.log("User list has changed:", newList);
            }
        }
    },
    created() {
        var vm = this;
        vm.GetAllEmployee();

    },
    computed: {

        filteredUserList() {
            const query = this.searchUserName.toLowerCase();
            return this.userList.filter(user => user.USER_NAME.toLowerCase().includes(query));
        },


    },
    methods: {

        ClosePopUp() {
            var vm = this;
            vm.$parent.isVisibleAddMemberPopUp = false;
        },

        selectAll() {

            var vm = this;
            let userDetails = []
            let userIds = '';

            let obj = {};
            vm.allSelected = !vm.allSelected;

            if (vm.allSelected) {

                vm.userList.forEach(ele => {
                    ele.isSelected = true;

                })
            } else {
                vm.userList.forEach(element => {

                    element.isSelected = false;

                })

            }


            vm.userList.forEach(ele => {

                if (ele.isSelected == true) {

                    obj = {
                        USER_NAME: ele.USER_NAME,
                        userId: ele.user_id
                    }


                    userDetails.push(obj);
                }
            })


            userIds = userDetails.map(item => item.userId).join(',');

            vm.getAddressValue(userIds);


        },


        select(event, item) {

            var vm = this;
            let userDetails = []
            let userIds = '';
            let obj = {};

            if (event.target.checked == true) {

                vm.userList.forEach(element => {
                    if (element.user_id == item.user_id) {
                        element.isSelected = true;
                        vm.allSelected = false;
                    } else {
                        //
                    }
                })

            }
            else {

                vm.userList.forEach(element => {

                    if (element.user_id == item.user_id) {
                        element.isSelected = false;
                        vm.allSelected = false;
                    } else {
                        //
                    }
                })


            }

            vm.userList.forEach(ele => {

                if (ele.isSelected == true) {
                    obj = {
                        USER_NAME: ele.USER_NAME,
                        userId: ele.user_id
                    }
                    userDetails.push(obj);
                }
            })

            if (vm.userList.length == userDetails.length) {
                vm.allSelected = true;
            }
            else {
                vm.allSelected = false;
            }

            userIds = userDetails.map(item => item.userId).join(',');

            vm.getAddressValue(userIds);

        },

        getAddressValue(userIds) {
            var vm = this;
            vm.userIds = '';
            vm.userIds = userIds;
            console.log("vm.userIds", vm.userIds);

        },




        async GetAllEmployee() {

            var vm = this
            try {

                let url = `userId=${vm.UserId}&type=${vm.type}`;

                await DataService.GetAllEmployee(url).then(response => {

                    let data = JSON.parse(response.data[0].Value).Data;
                    data.forEach(element => {
                        vm.userList.push(element);

                    })
                    vm.userList.forEach(element => {
                        element.isSelected = false
                    })

                    console.log("vm.userList", vm.userList);


                })

            } catch (error) {
                console.log("error message", error);
            }
        },


        async SavePrimaryMember() {
            var vm = this;
            if (vm.userIds == '') {


                vm.ShowAlert(vm.$t('Please select one/multiple checkbox(s)'), "warning", true, vm.$t("Alert"));

                return;
            }
            try {

                let url = `Pid=${vm.UserId}&EmpId=${vm.userIds}&Type=${vm.type}`;



                let response = await DataService.AddPrimaryAndSecondaryTeamMember(url);


                if (response.data && response.data !== '') {

                    if (response.data[0].Status == 'Data Saved Sucessfully!') {
                        if (vm.type === "Primary") {
                            vm.$parent.GetOrgReportTeam();
                            vm.ClosePopUp();
                        } else {
                            vm.$parent.GetOrgReportSecondaryTeam();
                            vm.ClosePopUp();
                        }
                    }
                    else {
                        //
                    }


                }

            } catch (error) {
                console.error("Error while saving primary member:", error);
            }
        }


    }
}
</script>